<template>
  <div class="music-row-title-order-container f-row-between">
    <div class="title f-i-c">
      <div class="shading" />
      <p class="row-title">{{ rowTitle }}</p>
      <p v-if="albumDetail" class="update-status">
        {{ albumDetail.isEndUpdate ? `${albumDetail.totalItemCount}节全` : `更新至${albumDetail.itemCount}节` }}
      </p>
    </div>
    <div @click="changeListOrder" class="total f-row-between-c">
      <img :src="listOrder === true ? sortPositive : sortReverse" alt="">
      <p>
        {{ listOrder === true ? '正序' : '倒序' }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    rowTitle: {
      type: String,
      default: '无标题'
    },
    listOrder: {
      type: Boolean,
      default: true
    },
    albumDetail: {
      type: Object,
      default: null
    }
  },

  data() {
    return {
      sortPositive: require( '@/assets/images/play_music_positive.png' ),
      sortReverse: require( '@/assets/images/play_music_reverse.png' )
    }
  },

  methods: {
    changeListOrder() {
      this.$emit( 'changeListOrder' );
    }
  }
}
</script>

<style lang="scss" scoped>
.music-row-title-order-container {
  width: 100%;
  height: 25px;

  .title {
    width: 80%;
    font-size: 18px;
    font-weight: bold;
    color: rgba(51,51,51,1);
    position: relative;

    .shading {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 45px;
      height: 8px;
      background: linear-gradient(90deg,rgba(244,186,65,1) 0%,rgba(255,255,255,1) 100%);
    }
    .row-title {
      position: relative;
    }
    .update-status {
      margin-left: 15px;
      font-size: 13px;
      color: rgba(170,170,170,1);
      font-weight: 400;
    }
  }
  .total {
    width: 42px;

    p {
      font-size: 13px;
      color: rgba(102,102,102,1);
    }
    img {
      width: 13px;
    }
  }
}
</style>


