<template>
  <div v-if="albumDetail" class="album-catalogue-container">

    <AlbumHeader
      class="album-header-box"
      @clickTitleRow="clickTitleRow"
      :albumInfo="albumDetail"
    />

    <div class="split-div-1" />

    <div v-if="musicList && musicList.length > 0" class="album-list-box">
      <div class="music-row-title-order-box">
        <AlbumMusicRowTitleOrder
          rowTitle="目录"
          :listOrder="listOrder"
          @changeListOrder="changeListOrder"
          :albumDetail="albumDetail"
        />
      </div>
      <div
        v-for="(item, itemIndex) in musicList"
        :key="item._id"
      >
        <MusicList
          :albumDetail="albumDetail"
          :section="item"
          :sectionIndex="itemIndex"
          @click="clickMusicList"
          class="music-list-box"
        />
      </div>
    </div>

    <!-- // -- 购买按钮 -->
    <div v-if="albumIsNeedBuy(albumDetail.chargeType, albumDetail.isBuy)" class="bottom-button-box">
      <van-button
        @click="buyAlbum"
        class="bottom-button"
        type="default"
        :text="toBuyButtonText"
        round
        color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
      />
    </div>

    <AlbumBuyPopup
      :isShowBuyPopup="isShowBuyPopup"
      @closeBuyPopup="closeBuyPopup"
      @clickConfirmBuy="clickConfirmBuy"
      :albumInfo="albumDetail"
    />

    <div style="width: 100%; height: 64px" />
  </div>
</template>

<script>
import AlbumHeader from '@/components/album/AlbumHeader.vue';
import MusicList from '@/components/musicList/MusicList.vue';
import AlbumBuyPopup from '@/components/album/AlbumBuyPopup';
import AlbumMusicRowTitleOrder from '@/components/album/AlbumMusicRowTitleOrder.vue';
import { apiAlbumDetail } from '@/service/api/program';
import { apiOrderPrePay, apiOrderSubmit } from '@/service/api/payment';
import { toDecimal2 } from '../../utils/money';
import { albumIsNeedBuy } from '../../utils/index';
import { Toast, Button } from 'vant';
import Vue from 'vue';
import wx from 'weixin-js-sdk';

Vue.use(Toast).use(Button);

export default {
  components: {
    AlbumHeader,
    MusicList,
    AlbumMusicRowTitleOrder,
    AlbumBuyPopup
  },

  data() {
    return {
      listOrder: true, // 列表排序
      musicList: null,
      positiveList: null,
      reverseList: null,
      
      albumDetail: null,
      toBuyButtonText: ' 去购买',

      isShowBuyPopup: false,
      
      fromRouteName: null
    }
  },

  created() {
    document.title = '青豆父母学堂';
  },

  mounted() {
    this.getAlbumDetail( this.$route.query.albumId );
  },

  methods: {
    // todo 判断用户是否需要购买当前专辑
    albumIsNeedBuy(chargeType, isBuy) {
      return albumIsNeedBuy(chargeType, isBuy);
    },

    closeBuyPopup() {
      this.isShowBuyPopup = false;
    },
    clickConfirmBuy() {
      this.isShowBuyPopup = false;
      this.buyAlbum();
    },

    // todo 购买逻辑
    buyAlbum() {
      this.openNewOrder();
    },
    async openNewOrder() {
      let spus = [{
        spuId: this.albumDetail.spuInfo.spuId,
        skuId: '',
        quantity: '1',
        spuName: this.albumDetail.name
      }];

      const newOrderResponse = await apiOrderSubmit({ spus });
      if (!newOrderResponse || newOrderResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('newOrderResponse: ', newOrderResponse);

      this.launchWechatPay(newOrderResponse.data.orderNo);
    },
    async launchWechatPay(orderNumber) {
      const prePayResponse = await apiOrderPrePay( orderNumber );
      if (!prePayResponse || prePayResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      console.log('prePayResponse: ', prePayResponse.data);

      let wechatConfig = prePayResponse.data;
      wechatConfig.jsApiList = ['chooseWXPay'];
      wechatConfig.timestamp = wechatConfig.timeStamp;
      try {
        wx.config(wechatConfig);
      } catch (error) {
        console.log('error: ', error);
      }

      wx.ready(function () {
        wx.chooseWXPay(wechatConfig);
      });
    },

    // todo 获取专辑详情
    async getAlbumDetail(albumId) {
      const albumDetailResponse = await apiAlbumDetail( albumId );
      if (!albumDetailResponse || albumDetailResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      console.log('albumDetailResponse: ', albumDetailResponse);
      this.albumDetail = albumDetailResponse.data;
      this.toBuyButtonText = '￥' + toDecimal2( this.albumDetail.spuInfo.price ) + this.toBuyButtonText;
      
      this.buildMusicList();
    },

    buildMusicList() {
      this.positiveList = this.albumDetail.items;
      this.musicList = this.albumDetail.items;
      this.reverseList = JSON.parse( JSON.stringify( this.positiveList ) ).reverse();
    },

    clickTitleRow() {
      if (this.$route.meta.fromRouteName === 'album-introduction') {
        this.$router.back();
      } else {
        this.$router.push({
          name: 'album-introduction',
          query: {
            albumId: this.$route.query.albumId
          }
        })
      }
    },

    changeListOrder() {
      this.listOrder = !this.listOrder;
      if (this.listOrder === true) {
        this.musicList = this.positiveList;
      } else {
        this.musicList = this.reverseList;
      }
    },

    clickMusicList(section, sectionIndex) {
      // ? 点击了目录中的条目
    }
  }
}
</script>

<style lang="scss" scoped>
.album-catalogue-container {
  width: 100%;
  height: 100%;
  background: rgba(246,246,246,1);
  
  .album-header-box {
    width: 100%;
    height: 160px;
    box-sizing: border-box;
    padding: 20px 16px;
    background-color: #fff;
  }

  .split-div-1 {
    width: 100%;
    height: 10px;
  }

  .album-list-box {
    width: calc(100% - 20px);
    margin: 0 10px;
    background-color: #fff;
    
    .music-row-title-order-box {
      width: 100%;
      box-sizing: border-box;
      padding: 25px 16px 5px;
    }
    .music-list-box {
      width: 100%;
      box-sizing: border-box;
      padding: 0 16px;
    }
  }

  .bottom-button-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px 20px 10px;
    background: #fff;
    z-index: 1;
    
    .bottom-button {
      width: 100%;
    }
  }
}
</style>


