<template>
  <div v-if="section" @click="clickSection(section, sectionIndex)" class="music-list-container">
    <div class="iterator-box">
      <div class="main-area f-row-between-c">
        <div
          :class="['order-number', (!albumDetail.isBuy && !section.isFree) || (sectionIndex >= albumDetail.itemCount) ? 'color-999' : '']"
        >
          {{ sectionIndex | fltCompletionNum }}
        </div>
        <div class="content f-col-between">
          <div
            :class="['title', playingSectionId === section._id ? 'color-playing' : '', (!albumDetail.isBuy && !section.isFree) || (sectionIndex >= albumDetail.itemCount) ? 'color-999' : '']"
          >
            <span v-if="section.isLastPlay" class="last-hear">
              上次听到
            </span>
            {{ section.title }}
            <span v-if="section.isFree" class="try-hear-label">
              试听
            </span>
          </div>
          <div class="complex-row">
            <span>
              {{ section.duration | fltTimeToMinute }}
            </span>
            <span>
              {{ `| ${section.playUv}人听过` }}
            </span>
            <!-- <span>
              {{ `| 已听完` }}
            </span> -->
          </div>
        </div>
        <div class="play-icon f-c">
          <!-- // ? 1. 待更新 -->
          <!-- // ? 2. 未购买 -->
          <!-- // ? 3. 可收听 -->
          <div v-if="sectionIndex >= albumDetail.itemCount" class="not-updated">
            待更新
          </div>
          <img v-else-if="!albumDetail.isBuy && !section.isFree" class="section-lock" src="@/assets/images/section_lock.png" alt="">
          <div v-else>
            <img v-if="playingSectionId === section._id && playStatus === true" src="@/assets/images/music_list_playing.gif" class="icon" alt="">
            <img v-else-if="playingSectionId === section._id && playStatus === false" src="@/assets/images/music_list_playing.png" class="icon" alt="">
            <img v-else src="@/assets/images/music_list_play.png" class="icon" alt="">
          </div>
        </div>
      </div>
      <div class="split-1" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { apiAlbumItemPlay, apiAlbumItemDetail } from '@/service/api/program';
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);

export default {
  props: {
    section: {
      type: Object,
      default: null
    },
    sectionIndex: {
      type: Number,
      default: 0
    },
    albumDetail: {
      type: Object,
      default: null
    }
  },
  
  computed: mapState({
    audio: state => state.audioState.audio,
    playStatus: state => state.audioState.playStatus,
    playbackRate: state => state.audioState.playbackRate,
    playingAlbumData: state => state.playingAlbumData,
    playingAlbumId: state => state.playingAlbumId,
    playingSectionId: state => state.playingSectionId,
    playingSectionData: state => state.playingSectionData
  }),

  data() {
    return {
      
    }
  },

  methods: {
    clickSection(section, sectionIndex) {
      // todo 存储即将播放的专辑详情数据，存储条目数据 -> 为了上下一节使用
      this.$store.commit('setPlayingAlbumData', { data: this.albumDetail });
      
      if (!this.albumDetail.isBuy && !section.isFree) return ;
      this.$emit( 'click', section, sectionIndex );
      
      this.checkIsChangeSection(this.albumDetail._id, this.section._id);
    },

    // todo 检测传进来的 albumId 和 sectionId 是否与当前状态中存储的不同，如果不同，那么播放它
    checkIsChangeSection(albumId, sectionId) {
      this.commitSectionDetail( sectionId );

      if (
        (this.playingSectionId === sectionId && this.playingAlbumId === albumId)
      ) {
        if (this.$route.name !== 'play-music') {
          this.goToPlayMusicPage();
        }
        return ;
      }

      this.playSection( albumId, sectionId );
    },

    // todo 获取并存储当前小节详情至状态管理中
    async commitSectionDetail(sectionId) {
      const sectionDetail = await this.getSectionDetail(sectionId);
      if (typeof sectionDetail !== 'object') return Toast('本小节音频错误，请联系客服人员');
      this.$store.commit('setPlayingSectionData', { data: sectionDetail });
    },

    // todo 获取小节详情
    async getSectionDetail(sectionId) {
      const sectionDetailResponse = await apiAlbumItemDetail(sectionId);
      if (!sectionDetailResponse || sectionDetailResponse.type === 'fail') return Toast('本小节音频错误，请联系客服人员');
      return sectionDetailResponse.data;
    },
    
    // todo 调用播放，因为跳转页面后点击行为就会失效，无法播放
    // todo 请求数据播放传进来的小节
    playSection(albumId, sectionId) {
      if (!this.playingAlbumData) return Toast( '意外错误，未得到专辑数据，请联系客服人员' );
      const section = this.playingAlbumData.items.find(item => {
        return item._id === sectionId;
      });
      const playSource = section.audio;
      this.audio.src = playSource;
      this.switchToPlaying();
      this.savePlayingData(albumId, sectionId);
      this.countSectionPlayTimes(albumId, sectionId);
      
      if (this.$route.name !== 'play-music') {
        this.goToPlayMusicPage();
      }
    },

    // todo 切换播放状态，修改状态管理中的音频播放状态
    switchToPlaying() {
      this.audio.play();
      this.$store.commit( 'setIsShowFloatWindow', { data: true } );
      this.$store.commit( 'audioState/setPlayStatus', { data: true } );
    },

    // todo 记录播放的专辑id 和 小节id 到状态管理中
    savePlayingData(albumId, sectionId) {
      this.$store.commit('setPlayingAlbumId', { data: albumId });
      this.$store.commit('setPlayingSectionId', { data: sectionId });
    },
    // todo 调用播放接口，统计播放次数
    async countSectionPlayTimes(albumId, sectionId) {
      const countResponse = await apiAlbumItemPlay(albumId, sectionId);
      if (!countResponse || countResponse.type === 'fail') return Toast('音频统计错误，请联系客服人员');
    },

    goToPlayMusicPage() {
      this.$router.push({
        name: 'play-music',
        query: {
          albumId: this.albumDetail._id,
          sectionId: this.section._id
        }
      });
    },
  }
}
</script>

<style lang="scss" scoped>
.music-list-container {
  width: 100%;
  
  .main-area {
    padding: 20px 0;
    width: 100%;
    box-sizing: border-box;

    .order-number {
      align-self: flex-start;
      position: relative;
      top: 2px;
      width: 20px;
      font-size: 15px;
      color: rgba(102,102,102,1);
    }

    .color-999 {
      color: #999999;
    }

    .content {
      width: calc(100% - 70px);

      .title {
        width: 100%;
        font-size: 15px;
        color: rgba(51,51,51,1);

        .last-hear {
          display: inline-block;
          line-height: 15px;
          text-align: center;
          width: 50px;
          height: 15px;
          background: rgba(240,60,60,1);
          border-radius: 2px;
          font-size: 10px;
          color: #fff;
          position: relative;
          top: -2px;
        }

        .try-hear-label {
          display: inline-block;
          line-height: 15px;
          text-align: center;
          width: 30px;
          height: 15px;
          background: rgba(255,252,246,1);
          border: 1px solid rgba(245,184,65,1);
          border-radius: 2px;
          font-size: 10px;
          color: rgba(245,184,65,1);
          position: relative;
          top: -2px;
        }
      }
      .color-999 {
        color: #999999;
      }
      .complex-row {
        margin-top: 6px;
        width: 100%;
        font-size: 12px;
        color: #999999;
      }
      .color-playing {
        color: #F4BA41;
      }
    }
    .play-icon {
      width: 50px;
      height: 30px;
      
      .not-updated {
        background: rgba(204,204,204,1);
        border-radius: 9px;
        font-size: 10px;
        color: rgba(255,255,255,1);
        padding: 2px 4px;
      }
      .section-lock {
        width: 20px;
        height: 22px;
      }
      .icon {
        width: 30px;
        height: 30px;
      }
    }
  }
  .split-1 {
    width: 100%;
    height: 1px;
    background: rgba(238,238,238,1);
  }
}
</style>


